@keyframes anim-opacity-99 { 0% { opacity: 0; }
  99% { opacity: 0; }
  100% { opacity: 1; } }

@keyframes fade_move_down { 0% { transform: translateY(-10px) rotate(45deg);
    opacity: 0; }
  50% { opacity: 1; }
  100% { transform: translateY(10px) rotate(45deg);
    opacity: 0; } }

.scroll-down { position: absolute; bottom: 2em; display: block; text-align: center; font-size: 20px; z-index: 100; text-decoration: none; text-shadow: 0; width: 43px; height: 43px; border-bottom: 2px solid #fff; border-right: 2px solid #fff; z-index: 9; left: calc(50% - 20px); transform: rotate(45deg); animation: fade_move_down 4s ease-in-out infinite; }

.text-center { text-align: center; }

.wrapper, .header { max-width: 1440px; padding: 0 20px; margin: 0 auto; position: relative; }

.header-out { position: fixed; top: 0; left: 0; width: 100%; background: white; z-index: 10; box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.2); }

main { margin-top: 100px; }

.header { display: flex; align-items: center; justify-content: space-between; padding: 1.3em 20px; max-height: 120px; }

.header__menu-icon { background: transparent; border: none; }

.header__menu-icon:focus { outline: none; }

@media only screen and (min-width: 769px) { .header__menu-icon { display: none; } }

.header__info { text-align: right; font-size: 14px; line-height: 16px; letter-spacing: 0.08em; font-style: normal; font-weight: normal; color: #777; display: none; }

@media only screen and (min-width: 769px) { .header__info { display: block; } }

.header__info a { color: #777; }

.header__info a:hover { color: #111111; }

.header-bg { background-image: url(../../img/header-darken.jpg); background-size: cover; background-attachment: fixed; background-position: center; background-repeat: no-repeat; display: flex; align-items: center; justify-content: center; position: relative; height: calc(100vh - 120px); z-index: -1; }

.header-bg svg { margin-top: -100px; }

.logo svg { max-width: 150px; fill: #093B66; }

.logo a { color: #1E127D; display: block; }

.nav { display: none; }

@media only screen and (min-width: 769px) { .nav { display: block; } }

.nav ul { display: flex; list-style-type: none; margin: 0; padding: 0; }

.nav li { font-style: normal; font-weight: normal; font-size: 16px; line-height: 19px; color: #777; text-transform: uppercase; flex-shrink: 0; }

.nav li + li { margin-left: 1em; }

@media only screen and (min-width: 992px) { .nav li + li { margin-left: 2em; } }

nav a { color: #777; }

nav a:hover { color: #111111; }

.welcome-text { text-align: center; max-width: 620px; margin: 0px auto; padding: 60px 0 80px 0; }

.welcome-text h3 { font-weight: bold; font-size: 32px; line-height: 37px; color: #1E127D; }

.welcome-text p { font-style: normal; font-weight: normal; font-size: 16px; line-height: 24px; color: #585858; }

.section { padding: 0; }

.section__title { font-style: normal; font-weight: normal; font-size: 48px; line-height: 56px; text-align: center; }

.services { background-color: #1B1F28; background-image: url(../../img/services-bg.jpg); background-size: cover; padding: 10px 0 50px 0; }

.services h3 { color: #FAFAFA; }

.services__list { display: flex; justify-content: space-between; flex-wrap: wrap; }

.services__item { flex-basis: 100%; margin: 0 40px; text-align: center; margin-bottom: 70px; }

@media only screen and (min-width: 769px) { .services__item { flex-basis: calc(50% - 80px); } }

@media only screen and (min-width: 992px) { .services__item { flex-basis: calc(33% - 80px); } }

.cars { margin-bottom: 100px; overflow: hidden; }

.cars__list { display: flex; margin: 0 -30px; flex-wrap: wrap; align-items: center; justify-content: center; align-items: stretch; }

@media only screen and (min-width: 992px) { .cars__list { justify-content: space-between; } }

.cars__item { flex-basis: 100%; margin: 0 40px; margin-bottom: 40px; background: #FAFAFA; border: 1px solid #C4C4C4; box-sizing: border-box; border-radius: 2px; }

@media only screen and (min-width: 769px) { .cars__item { flex-basis: calc(50% - 80px); } }

@media only screen and (min-width: 992px) { .cars__item { flex-basis: calc(33% - 80px); } }

.cars__item:hover { box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2); cursor: pointer; }

.car__img { height: 384px; width: 100%; object-fit: cover; }

.car__content { padding: 0px 40px 40px 40px; }

.car__name { font-style: normal; font-weight: bold; font-size: 20px; line-height: 23px; color: #1E127D; }

.car__price { font-size: 3em; margin: 0.5em 0; }

.car__table { width: 100%; margin-bottom: 70px; }

.car__table th { text-transform: uppercase; }

.car__table th, .car__table td { text-align: left; font-weight: bold; font-size: 16px; line-height: 19px; color: #C4C4C4; width: 50%; }

.car__table td { color: #1B1F28; }

.car__link { color: #FAFAFA; background: #1B1F28; border: 1px solid #FAFAFA; }

.service__title { font-weight: bold; font-size: 24px; line-height: 28px; color: #FAFAFA; }

.service__text { font-size: 16px; line-height: 24px; color: #FAFAFA; margin-bottom: 40px; }

.service__link { background: #1B1F28; border: 1px solid #FAFAFA; color: #FAFAFA; }

.btn-link { border-radius: 2px; padding: 15px 36px; font-size: 16px; line-height: 19px; text-align: center; }

#contact { background: #1b1f28; padding: 40px 0 0 0; position: relative; }

#contact .two-col { display: flex; align-items: center; height: 450px; }

#contact iframe { border: 0; width: 50%; height: 100%; }

#contact .text-center { width: 50%; }

#contact h3 { margin-bottom: 90px; color: white; }

#contact p { font-size: 18px; line-height: 21px; color: #FAFAFA; }

#contact a { color: #FAFAFA; }

.copyright { font-style: normal; font-weight: bold; font-size: 24px; line-height: 28px; color: #C4C4C4; text-align: center; }

.copyright svg { fill: #093b66; }

.is-mobile-overlay-active { position: fixed; width: 100%; }

.Mobile-overlay { position: fixed; top: 0; left: -99999px; bottom: 0; z-index: 1010; width: 100%; transition: left 0s 355ms, background-color 350ms cubic-bezier(0.55, 0, 0.1, 1) 175ms; animation: anim-opacity-99 525ms linear; }

.Mobile-overlay ul { padding: 0; }

.Mobile-overlay .header__info { text-align: left; display: block; }

.Mobile-overlay .menu-item { list-style-type: none; }

.Mobile-overlay .menu-item a { font-weight: 500; font-style: normal; font-size: 16px; line-height: 2.4em; text-transform: uppercase; color: #707070; }

.is-mobile-overlay-active .Mobile-overlay { left: 0; background-color: #fafafa; transition: left 0s, background-color 350ms cubic-bezier(0.55, 0, 0.1, 1); }

.enable-load-effects .Mobile-overlay { animation: anim-opacity-99 525ms linear; }

.Mobile-overlay-back { position: absolute; top: 0; right: 0; bottom: 0; left: 0; z-index: 0; }

.Mobile-overlay-close { outline: 0; border: 0; padding: 0; background-color: transparent; text-transform: inherit; letter-spacing: inherit; position: absolute; top: 0; right: 0; display: block; z-index: 1; padding: 22px; line-height: 0; stroke: transparent; transition: stroke 350ms cubic-bezier(0.55, 0, 0.1, 1), background-color 350ms cubic-bezier(0.55, 0, 0.1, 1); cursor: pointer; }

.is-mobile-overlay-active .Mobile-overlay-close { background-color: #000; stroke: #fff; transition: stroke 350ms cubic-bezier(0.55, 0, 0.1, 1) 175ms, background-color 350ms cubic-bezier(0.55, 0, 0.1, 1) 175ms; }

.Mobile-overlay-menu-main { box-sizing: border-box; position: absolute; top: 0; bottom: 0; z-index: 3; width: 100%; padding: 36px; overflow: auto; transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1); }

.Mobile-overlay-close .Icon--close { display: block; stroke-width: 3; stroke: transparent; }

.Mobile-overlay-menu { position: absolute; top: 0; bottom: 0; z-index: 2; width: 80%; width: calc(100% - 60px); overflow: hidden; background-color: #fafafa; transform: translate3d(-100%, 0, 0); transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1) 175ms; }

.is-mobile-overlay-active .Mobile-overlay-menu { transform: translate3d(0, 0, 0); transition: transform 350ms cubic-bezier(0.55, 0, 0.1, 1); }
